import evcTabbar from '@/components/evcTabbar.vue'
export default {
	components: {
				evcTabbar	
			},
	data() {
		return {
			fontColor4: '#0099FF',
			myImgSelect: '../../../static/images/evctabbar/myselect.png',
			nickname: '',
			avatar: '',
			status:'',
			planLevel:''
		}
	},
	onNavigationBarButtonTap(e) {
		if (e.index == 0) {
			uni.navigateTo({
				url: '../message/message'
			})
		}
	},

	onLoad() {
		//获取用户信息
		uni.request({
			url: this.baseUrl + "/member-info",
			header: {
				//除注册登录外其他的请求都携带用户token和秘钥
				Authorization: uni.getStorageSync('token')
			},
			success: (res) => {
				console.log(res.data)
				if (this.$base1._indexOf(res.data.status)) {
					this.$base1._isLogin()
				} else if (res.data.status == 1) {
					this.nickname = res.data.data.NickName
					this.avatar ='http://ceshi.8kpay.com/' + res.data.data.Avatar
					this.status = res.data.data.IsForbidden
					this.planLevel = res.data.data.PlanLevel
					
				} else {
					uni.showToast({
						title: res.data.message,
						icon: "none"
					})
				}
			}
		})
	},
	methods: {
		showLevelBgc(level){
			if(level==1){
				return  'linear-gradient(#FF727C, #FFA8AE)'
			}else if(level==2){
				return  'linear-gradient(#7FCCFF, #0099FF)'
			}else if(level==3){
				return  'linear-gradient(#FFC744, #FF9100)'
			}
		},
		jumpToInvite(){
			uni.navigateTo({
					url: "./invite"
			})
		},
		jumpToPersonalInfo() {
			uni.navigateTo({
				url: "./personal-info"
			})
		},
		unsealing(){
			uni.navigateTo({
				url:"./unsealing"
			})
			
		},
		jumpToRealName(){
			uni.navigateTo({
				url:"./real-name"
			})
			
		},
		jumpToMyBill(){
			uni.navigateTo({
				url:"./my-bill"
			})
		}
	}
}
